import React, { Fragment } from 'react';
import { Formik, Form } from "formik";
import * as Yup from "yup";
import "./style.css";
import FormikControl from '../Formik/FormikControl';
import Button from '../Buttons/Button';
import { useToasts } from 'react-toast-notifications'
import axios from 'axios';



const initialValues = {
    name: '',
    mobile: '',
    email: '',
    message: '',
}
const phoneRegExp = /^[6-9]\d{9}$/;
const validationSchema = Yup.object({
    name: Yup.string().required("Required!"),
    mobile: Yup.string().matches(phoneRegExp, 'Phone number is not valid').required("Required!"),
    email: Yup.string().email("Enter valid email address").required('Required'),
    message: Yup.string().required("Required!"),
});

const FooterForm = (props) => {
    const { addToast } = useToasts()

    const onSubmit = async (values, onSubmitProps) => {
        const { name, email, mobile, message } = values;
        const data = {
            name,
            email,
            mobile,
            message
        }
        const BASE_URL = `https://api.doortodumps.com/wms`;

        const headers = {
            "Content-Type": "application/json"
        }
        try {
            await axios.post(`${BASE_URL}/api/v1/visitors`, data, headers);
            addToast('Saved Successfully', { appearance: 'success', autoDismiss: true })
        }
        catch (err) {
            addToast(err.message, { appearance: 'error', autoDismiss: true })
            console.log(err)
        }

        onSubmitProps.resetForm();
    }
    return (
        <Fragment>
            <Formik initialValues={initialValues} onSubmit={onSubmit} validationSchema={validationSchema} >
                <Form className="formContainer">
                    <FormikControl
                        type="text"
                        placeholder="Your name"
                        name="name"
                        className="fs-14 customInput"
                        control="input"
                        errClass="error"
                    />
                    <FormikControl
                        type="text"
                        placeholder="Mobile Number"
                        name="mobile"
                        className="fs-14 customInput"
                        control="input"
                        errClass="error"
                    />
                    <FormikControl
                        type="text"
                        placeholder="Your Email"
                        name="email"
                        className="fs-14 customInput"
                        control="input"
                        errClass="error"
                    />
                    <FormikControl
                        type="text"
                        placeholder="Your message"
                        name="message"
                        className="fs-14 customInput"
                        control="textarea"
                        errClass="error"
                    />
                    <Button btnText="Submit" className="buttonTextWhite bg-blue buttonArea" />
                </Form>
            </Formik>
        </Fragment>
    );
};

export default FooterForm;